import React from "react";
import AdminMenu from "../../components/AdminMenu/AdminMenu";
import Nav from "../../components/Navbar/Nav";
import { useState, useEffect } from "react";
import { useAuth } from "../../Context/auth";
import toast from "react-hot-toast";
import moment from "moment";
import axios from "axios";
import { FaRupeeSign } from "react-icons/fa";
import { Select } from "antd";
const { Option } = Select;

const AdminOrders = () => {
  const [orders, SetOrders] = useState([]);
  const [auth] = useAuth();
  const [status, SetStatus] = useState([
    "Not process",
    "processing",
    "Shipped",
    "deliver",
    "cancel",
  ]);
  // const [changeStatus, SetChangeStatus] = useState("");

  const getOrders = async () => {
    try {
      const { data } = await axios.get("/api/auth/all-orders");
      SetOrders(data);
    } catch (error) {
   toast.error("someting went wrong")
    }
  };
  useEffect(() => {
    if (auth?.token) getOrders();
  }, [auth?.token]);
  const HandleChange = async (orderId, value) => {
    try {
      const { data } = await axios.put(`/api/auth/orders-status/${orderId}`, {
        status: value,
      });
      if (data) {
        getOrders();

        toast.success("status updated successfully");
      } else {
        toast.error("status not updated");
      }
    } catch (error) {

      toast.error("something went wrong");
    }
  };
  return (
    <>
      <div>
        <Nav />

        <div className="admin_orders_main">
          <hr />
          <div className="d-flex row container-fluid p-3 ">
            <div className="col-lg-2 col-sm-12">
              <AdminMenu />
            </div>
            <div className="col-lg-10 col-sm-12 ps-5  ">
              <div className=" p-3 w-85 ps-5 ">
                <h1 className="text-center text-uppercase font-monospace">
                  all orders
                </h1>
                {orders
                  ?.map((orders, i) => {
                    return (
                      <>
                        <div>
                          <table className="table">
                            <thead>
                              <tr>
                                <th scope="col">SR NO</th>
                                <th scope="col">status</th>
                                <th scope="col">buyer ID</th>
                                <th scope="col">orders</th>
                                <th scope="col">quantity</th>
                                <th scope="col">payment</th>
                                <th scope="col">total</th>
                              </tr>
                            </thead>
                            <tbody>
                              <tr>
                                <td>{i + 1}</td>
                                <td>
                                  <Select
                                    bordered={false}
                                    onChange={(value) =>
                                      HandleChange(orders._id, value)
                                    }
                                    defaultValue={orders?.status}
                                  >
                                    {status.map((s, i) => (
                                      <Option key={i} value={s}>
                                        {s}
                                      </Option>
                                    ))}
                                  </Select>
                                </td>
                                <td>{orders?.buyer?._id}</td>

                                <td>{moment(orders?.createAt).fromNow()}</td>
                                <td>
                                  {orders?.payment.success ? (
                                    <span className="bg-success">success</span>
                                  ) : (
                                    <span className="bg-danger ">failed</span>
                                  )}
                                </td>
                                <td>{orders?.products?.length}</td>
                                <td>
                                  {" "}
                                  <FaRupeeSign className="h6 mt-1" />
                                  {orders?.amount}/-
                                </td>
                              </tr>
                            </tbody>
                          </table>
                          <div className=" container">
                            {orders?.products?.map((product, i) => (
                              <>
                                <div
                                  className="row flex-row   mb-3 rounded-4"
                                  style={{
                                    boxShadow:
                                      "rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px",
                                  }}
                                >
                                  <div className="col-md-3 p-3">
                                    <img
                                      src={`/api/admin/product/product-photo/${product._id}`}
                                      className="  rounded-2"
                                      alt={product.productName}
                                      height={70}
                                      width={70}
                                    />
                                  </div>
                                  <div className="col-md-8 p-3">
                                    <h3 className="text-capitalize fw-bold font-monospace">
                                      {product.productName}
                                    </h3>
                                    <hr />
                                    {/* <p>{product.description}</p> */}
                                    <div className="d-flex flex-column flex-wrap ">
                                      <h4 className="fw-bold">
                                        {" "}
                                        <FaRupeeSign className="h5 mt-1" />
                                        {product.price}/-
                                      </h4>
                                    </div>
                                  </div>
                                </div>
                              </>
                            ))}
                          </div>
                        </div>
                      </>
                    );
                  })
                  .reverse()}
              </div>
            </div>
          </div>
        </div>

        <br />
      </div>
    </>
  );
};

export default AdminOrders;
